import { motion } from 'framer-motion'
import React from 'react'
import { NavLink } from 'react-router-dom'
import styled from 'styled-components'
import { Email, LinkedIn, GitHub } from '../components/AllSvgs'
import {DarkTheme} from '../components/Themes'

const Icons = styled.div`
display: flex;
flex-direction: column;
align-items: center;
position: fixed;
bottom: 0;
left: 2rem;
z-index:3;
&>*:not(:last-child){
    margin: 0.5rem 0;
}`
const Icon = styled.div`
&:hover{
    transition: all 0.3s ease;
    scale: 2;
}`
const Line = styled(motion.span)`
width: 3px;
height: 8rem;
background-color: ${props => props.color === 'dark' ? DarkTheme.text : DarkTheme.text  };`

const SocialIcons = (props) => {
    return (
        <Icons>
            <Icon>
                <motion.div
                    initial={{transform:"scale(0)"}}
                    animate={{scale:[0,1,1.5,1]}}
                    transition={{type:'spring', duration:1, delay:1}}>
                        <NavLink style={{color:'inherit'}}  target="_blank"   to={{pathname:"mailto:bushra.ebox@gmail.com"}}>
                            <Email width={25} height={25} fill={props.theme === "dark" ? DarkTheme.text  : DarkTheme.text  } />
                        </NavLink>
                </motion.div>
            </Icon>
            <Icon>
                <motion.div
                    initial={{transform:"scale(0)"}}
                    animate={{scale:[0,1,1.5,1]}}
                    transition={{type:'spring', duration:1, delay:1.2}}>
                        <NavLink style={{color:'inherit'}}  target="_blank"   to={{pathname:"https://linkedin.com/in/dev-bushra"}}>
                            <LinkedIn width={25} height={25} fill={props.theme === "dark" ? DarkTheme.text  : DarkTheme.text  } />
                        </NavLink>
                </motion.div>
            </Icon>
            <Icon>
                <motion.div
                    initial={{transform:"scale(0)"}}
                    animate={{scale:[0,1,1.5,1]}}
                    transition={{type:'spring', duration:1, delay:1.4}}>
                        <NavLink style={{color:'inherit'}}  target="_blank"   to={{pathname:"https://github.io/dev-bushra"}}>
                            <GitHub width={25} height={25} fill={props.theme === "dark" ? DarkTheme.text  : DarkTheme.text  } />
                        </NavLink>
                </motion.div>
            </Icon>
            <Line color={props.theme} initial={{height:0}} animate={{height: '8rem'}} transition={{type:'spring', duration:1, delay:0.8}} />
        </Icons>
    )
}

export default SocialIcons
